.container {
  max-width: 700px;
  margin: auto;
  .itemContainer {
    margin: auto;
    margin-bottom: 100px;
    min-height: 400px;

    &:nth-of-type(1) {
      margin-top: 80px;
    }

    @media screen and (max-width: 575px) {
      margin-bottom: 150px;

      img {
        margin-bottom: 30px;
      }
    }
  }

  h3 {
    font-style: normal;
    font-weight: 900;
    font-size: 36px;
    line-height: 44px;

    color: #45B5AA;

    text-shadow: 2px 2px 0px #E0E0E0;
  }

  p {
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 20px;
    max-width: 350px;

    @media screen and (max-width: 991px) {
      max-width: none;
    }

    color: #454545;
    // white-space: pre-wrap;
  }
}
